import React from 'react'

import classes from './menu.module.scss'
import Info from '../../UI/info/info'
import {NavLink} from 'react-router-dom'
import {connect} from 'react-redux'

import {logout} from '../../store/actions/auth'
import Logout from '../Logout/Logout'

class Header extends React.Component {



    render(){
        const {titleName, titleChar, titleState, switchOnHeader} = this.props



        let header;
        let menuSwitch;


            if(titleName === 'Главная'){
                header = classes.titleName_active
            } else if (titleName === 'Персонажи'){
                header = classes.titleName_active2
            } else if (titleName === 'Настройки'){
                header = classes.titleName_active3
            }



            if(titleState === 'true'){
                menuSwitch = ''
            } else if(titleState === 'false'){
                header = classes.titleName_active4
                menuSwitch = classes.menu_char
            }



        return (
            <div className={classes.header}>
                <div className={classes.wrapperMenu}>

                    <div className={classes.menu_header + ' ' + menuSwitch}>

                        <h1 className={classes.titleName + ' ' +header}>{titleName}</h1>



                        <div className={classes.titleChar}>
                            <NavLink onClick={switchOnHeader} className={classes.goBack} to="/characters">&larr;</NavLink>
                            <h1 className={classes.charTitle}>{titleChar}</h1>
                        </div>
                    </div>

                </div>
                <NavLink className={classes.link} to="/logout" >Выход</NavLink>
                <Info showPopupTwo={this.props.showPopup}/>
            </div>
        )
    }

}

function mapStateToProps(state){
  return {
    isAuhtenticated: !!state.auth.token
  }
}

export default connect(mapStateToProps)(Header)
