import React,{useState} from 'react'
import CharInfo from './char/char'
import Search from '../../UI/search/search'
import classes from './characters.module.scss'


const Characters = ({headerSw, charData}) => {

    const [char, setChar] = useState(charData)
    const [filter, setFilter] = useState(charData)
    const [currentCard, seTCurrentCard] = useState(null)

    function mainDropHandler(e,item) {
      setFilter(filter.map(c =>{
        if(c.id === item.id){
          return {...c, order: currentCard.order}
        }
        if (c.id === currentCard.id) {
          return {...c, order: item.order}
        }
        return c
      }))
      setChar(filter.map(c =>{
        if(c.id === item.id){
          return {...c, order: currentCard.order}
        }
        if (c.id === currentCard.id) {
          return {...c, order: item.order}
        }
        return c
      }))
      e.target.style.opacity = '1'
    }

    const  sendValue = (value) => {
        const filteredArray = filter.filter(names => {
          return names.name.toLowerCase().includes(value.toLowerCase())
        })
        setChar(filteredArray)
      }
    return(
        <div className={classes.charactersPage}>
            <Search sendValue={sendValue}/>
            <CharInfo filter={char} headerSw={headerSw} mainDropHandler={mainDropHandler} seTCurrentCard={seTCurrentCard}/>
        </div>
    )
}

export default Characters;
