const News = () => {

      const [photos, setPhotos] = useState([])
      const [fetching, setFetching] = useState(true)
      const totalCount  = data.length



        useEffect(()=>{
          if(fetching) {
                   arrayPromise.then(result => {

                   let newArray = data.slice(photos.length, photos.length + 3)
                   setPhotos([...photos, ...newArray])

                 }, error => {
                    console.log("Rejected: " + error.message);
                })
                .finally(() => setFetching(false));
             }
        },[fetching])

        useEffect(()=>{
          document.addEventListener('scroll', scrollHandler)
          return function (){
            document.removeEventListener('scroll', scrollHandler)
          }
        },[])

    const arrayPromise = new Promise((resolve, reject) => {
          resolve('sucsses');
    });

    const scrollHandler = (e) => {
      if(e.target.documentElement.scrollHeight - (e.target.documentElement.scrollTop + window.innerHeight) < 50
      && photos.length < totalCount) {
        setFetching(true)
      }
    }

    return (
        <div className={classes.news_parent}>
        {photos.map(item =>
          <div key={'id'+ item.id} className={classes.newsItem}>
              <a href="#">
                  <h3>{item.title}</h3>
                  <p>{item.text}</p>
                  <img src={'/images/'+ item.img}   alt=""/>
              </a>
          </div>
        )
        }
        </div>
    )
}

