import React,{useState} from 'react'
import {NavLink} from 'react-router-dom'

import classes from './char.module.scss'

// import CharacterPage from './pages/character-page/character-page'
// import { Route} from 'react-router-dom'


const CharInfo = ({filter, headerSw, seTCurrentCard, mainDropHandler}) => {

    // const [charList, setCardList] = useState(filter)
    // const [currentCard, seTCurrentCard] = useState(null)

    function dragStartHandler(e,item) {
      seTCurrentCard(item)
    }

    function dragEndHandler(e,item) {
      e.target.style.opacity = '1'
    }

    function dragOverHandler(e,item) {
      e.preventDefault();
      e.target.style.opacity = '0.3'
    }

    function dropHandler(e,item) {
      e.preventDefault();
      mainDropHandler(e, item)
    }

    const sortCards = (a,b) => {
      if(a.order > b.order) {
        return 1
      } else {
        return -1
      }
    }

    const charItem = filter.sort(sortCards).map((item) =>{
        return (
            <NavLink
                exact
                key={'id'+ item.charId}
                className={classes.char_item}
                id={item.charId}
                to={"/characters/" +  item.charId}
                onClick={(evt) => headerSw(item.name)}

                draggable={true}
                onDragStart={(e)=>dragStartHandler(e, item)}
                onDragLeave={(e)=>dragEndHandler(e, item)}
                onDragEnd={(e)=>dragEndHandler(e,item)}
                onDragOver={(e)=>dragOverHandler(e,item)}
                onDrop={(e)=>dropHandler(e,item)}
                >
                <img  src={'/images/chars/'+ item.imgName}   alt=""/>
                <h4>{item.name}</h4>
            </NavLink>
        )
    })
    return(
        <div className={classes.char_parent}>{charItem}</div>
    )
}

export default CharInfo;
