import React from 'react'

import classes from './settings.module.scss'

const Settings = () => {
    return(
        <div className={classes.settings}>
            <div className={classes.onBtn}>Оставлять экран включенным <span><i></i></span> </div>
            <div>
                <div>Выбрать платформу</div>
                <ul>
                    <li>PS4</li>
                    <li>XBOX</li>
                    <li>PC</li>
                    <li>SWITCH</li>
                    <li>Универсальная раскладка</li>
                </ul>
            </div>
            <div>Сообщить об ошибке</div>
            <div>О приложении</div>
        </div>
    )
}

export default Settings;
