import React from 'react';

import classes from './popup-info.module.scss'
import './popup-info.module.scss'

const InfoPopup = (props) =>{
    const {popupState, popupOff} = props;

    let classNames = '';
    if(popupState) {
        classNames = classes.modalWrapper + ' ' + classes.active;
    } else {
        classNames = classes.modalWrapper;
    }


    return(
        <div className={classNames} onClick={popupOff}>
            <div className={classes.popup} onClick={e => e.stopPropagation()}>
                    <div  style={{ backgroundImage: 'url(/images/combo/FB.png)' }}>
                        <p>Фатальный удар</p>
                    </div>
                    <div style={{ backgroundImage: 'url(/images/combo/crashingblow.png)' }}>
                        <p>Сокрушительный удар</p>
                    </div>
                    <div style={{ backgroundImage: 'url(/images/combo/force.png)' }}>
                        <p>Уселение</p>
                    </div>
                    <div style={{ backgroundImage: 'url(/images/combo/zahvat.png)' }}>
                        <p>Захват</p>
                    </div>
            </div>
        </div>
    )
}


export default InfoPopup;
