import React, {useState, useEffect, useRef} from 'react'

import {NavLink} from 'react-router-dom'
import {connect} from 'react-redux'
import {logout} from '../../store/actions/auth'
import Logout from '../Logout/Logout'
import classes from './sidebar.module.scss'



const Sidebar = (props) => {

  const [isScrolling, setIsScrolling] = useState(false)
  const [clientX, setClientX] = useState()
  const [scrollX, setScrollX] = useState(-380)

  const [animState, setAnimState] = useState(false);
  const [mouseDown, setMouseDown] = useState(false);

  let ref = useRef();
  let openAnimate = useRef();

  const onMouseDownHandler = (e) => {
    if (ref && ref.current && !ref.current.contains(e.target)) {
      return;
    }
    e.preventDefault();
        if(animState === false) {

            setMouseDown(true)
            setIsScrolling(true)
            setClientX(e.clientX)

        } else if(animState === true) {
          setMouseDown(false)
        }
  }
  const onMouseMoveHandler = (e) => {
    if (ref && ref.current && !ref.current.contains(e.target)) {
      return;
    }
    e.preventDefault();

    if (isScrolling === true) {
        let sX = scrollX + e.clientX - clientX;
        let cX = e.clientX;

        if (sX < -380) {
          sX = -380;
        } else if (sX > 0) {
          sX = 0;
        } else if(sX >= -380 && sX <= 0) {
            ref.current.style.left = sX + 'px';
            setClientX(cX)
            setScrollX(sX)
            console.log(sX)
        }
    }
  }
  const onMouseUpHandler = (e) => {
    endEventHandler(e)
  }
  const onMouseLeaveHandler = (e) => {
    endEventHandler(e)
  }

  const touchStarts = (e) => {

    if (ref && ref.current && !ref.current.contains(e.target)) {
      return;
    }
    e.preventDefault();
    if(animState === false) {
        setMouseDown(true)
        setIsScrolling(true)
        setClientX(e.touches[0].clientX)

  } else if(animState === true) {
    setMouseDown(false)
  }
  }
  const touchMove = (e) => {
    if (ref && ref.current && !ref.current.contains(e.target)) {
      return;
    }
    e.preventDefault();

    if (isScrolling === true) {
        let sX = scrollX + e.touches[0].clientX - clientX;
        let cX = e.touches[0].clientX;

        if (sX < -380) {
          sX = -380;
        } else if (sX > 0) {
          sX = 0;
        } else if(sX >= -380 && sX <= 0) {
            ref.current.style.left = sX + 'px';
            setClientX(cX)
            setScrollX(sX)
        }
    }
  }
  const touchEnd = (e) => {
    endEventHandler(e)
  }

  const endEventHandler = (e) =>{
      if (isScrolling === true) {
        if(animState === false) {
          if (ref && ref.current && !ref.current.contains(e.target)) {
            return;
          }
          e.preventDefault();
          setIsScrolling(false)

          if(scrollX > -380 && scrollX < 0 ) {
            cancelAnimationFrame(openAnimate.current);
            openAnimate.current = requestAnimationFrame(() =>  animateOpen(scrollX));
          }
        }
    }
  }
  const animateOpen = (cur) =>{
    setAnimState(true);

    openAnimate.current = requestAnimationFrame(() =>
      animateOpen(cur)
    );

    let dir;
    let rez;
    let speed;

    if(scrollX < -190) {
      dir = 'left'
      rez = -380
      speed = (380 + cur) /10
    } else if(scrollX >= -190) {
      dir = 'right'
      rez = 0
      speed = ((-380 - cur)*-1)/30;
    }


    cur = dir === "left" ? cur - speed : cur + speed;
    ref.current.style.left = cur.toFixed(2) + 'px';

    if( (Math.round(cur.toFixed(2)) === rez && dir === 'left') || (Math.round(cur.toFixed(2)) >= rez && dir === 'right')) {
    cancelAnimationFrame(openAnimate.current);
    ref.current.style.left = rez + 'px';

      setAnimState(false);
      setMouseDown(false);
      setScrollX(rez)

    }


  }

  useEffect(() => {
    document.addEventListener("mousedown", onMouseDownHandler);
    document.addEventListener("mouseup", onMouseUpHandler);
    document.addEventListener("mousemove", onMouseMoveHandler);
    document.addEventListener("mouseleave", onMouseLeaveHandler);

    document.addEventListener("touchstart", touchStarts);
    document.addEventListener("touchmove", touchMove);
    document.addEventListener("touchend", touchEnd);
    return () => {
      document.removeEventListener("mousedown", onMouseDownHandler);
      document.removeEventListener("mouseup", onMouseUpHandler);
      document.removeEventListener("mousemove", onMouseMoveHandler);
      document.removeEventListener("mouseleave", onMouseLeaveHandler);

      document.removeEventListener("touchstart", touchStarts);
      document.removeEventListener("touchmove", touchMove);
      document.removeEventListener("touchend", touchEnd);
    };
  });

  useEffect(() => {
    if(ref.current === true){
      openAnimate.current = requestAnimationFrame(() => animateOpen(0, 0, 0));
    }
    return () => {
      cancelAnimationFrame(openAnimate.current);
    };
  }, []);


  return (
    <div
      ref={ref}
      onMouseDown={onMouseDownHandler}
      onMouseUp={onMouseUpHandler}
      onMouseMove={onMouseMoveHandler}
      onMouseLeave={onMouseLeaveHandler}

      className={classes.sidebarWrap}
    >
      <div className={classes.sidebar}>
        <NavLink className={classes.link} to="/logout" >Выход</NavLink>
      </div>
    </div>
  )
}

export default Sidebar;
