import React,{ Component} from 'react'
import {NavLink} from 'react-router-dom'

import classes from './menu.module.scss'

import bg1 from "../../icons/home.png"
import bg2 from "../../icons/char.png"
import bg3 from "../../icons/settings.png"

import bg11 from "../../icons/homea.png"
import bg22 from "../../icons/chara.png"
import bg33 from "../../icons/settingsa.png"

class Menu extends  Component {

    render() {

    const {headerSwitch} = this.props

    return (
        <ul className={classes.menu}>
        <li onClick={(evt) => headerSwitch('Главная')}>
                <NavLink
                    className={classes.link}
                    activeClassName={classes.wfm_active}
                    exact
                    style={{backgroundImage: `url(${bg1})`}}
                    to="/"
                    activeStyle={{
                        backgroundImage: `url(${bg11})`
                    }}
                    >
                    Главная</NavLink>
            </li>
            <li onClick={(evt) => headerSwitch('Персонажи')}>
                <NavLink
                    className={classes.link}
                    activeClassName={classes.wfm_active}
                    style={{backgroundImage: `url(${bg2})`}}
                    to="/characters"
                    activeStyle={{
                        backgroundImage: `url(${bg22})`
                    }}
                    >Персонажи</NavLink>
            </li>
            <li onClick={(evt) => headerSwitch('Настройки')}>
                <NavLink
                className={classes.link}
                activeClassName={classes.wfm_active}
                exact style={{backgroundImage: `url(${bg3})`}}
                to="/settings"
                activeStyle={{
                    backgroundImage: `url(${bg33})`
                }}
                >Настройки</NavLink>
            </li>
        </ul>
    );
}
}

export default Menu;
